@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
.nav-search {
    overflow: hidden;
    font-family: sans-serif;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
}

.nav-search .search-bar {
    display: flex;
    overflow: hidden;
    align-items: center;
    border-radius: 4px;
    border: 1px solid #49a167;
}

.nav-search .search-box input[type="text"] {
    outline: none;
    border: 0;
    padding: 8px;
    font-size: 1em;
}

.nav-search .search-btn button {
    outline: none;
    border: none;
    color: white;
    font-weight: bold;
    font-size: 1em;
    background: #49a167;
    cursor: pointer;
    padding: 8px;
}

@media (max-width : 550px) {
    .nav-search .search-box input[type="text"] {
        padding: 4px;
        font-size: 0.8em;
    }
    
    .nav-search .search-btn button {
        font-size: 0.8em;
        padding: 4px;
    }
    
}
.navbar {
    width: 100%;
    overflow: hidden;
    background: #1b1b1b;
    height: auto;
    padding: 10px 0;
    margin: 0 auto;
    font-family: sans-serif;
}

.navbar .container {
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navbar .nav-left {
    flex-basis: 50%;
}
.navbar .logo {
    max-width: 8rem;
    overflow: hidden;
    font-size: 100%;
}
.navbar .logo a {
    display: block;
    text-decoration: none;
    color: white;
}
.navbar .logo .img-wrapper {
    margin: 0 auto;
    text-align: center;
}
.navbar .logo img {
    width: 100%;
}
.navbar .logo span {
    font-size: 1.3rem;
    display: block;
    margin: 0 auto;
    text-align: center;
    font-family: 'Shadows Into Light', cursive;
}

.navbar .search {
    flex-basis: 50%;
    display: flex;
    justify-content: flex-end;
}

.navbar .search .label {
    color: white;
    margin-bottom: 5px;
    font-size: 0.8em;
}


@media (max-width : 550px) {
    .navbar .logo {
        max-width: 3rem;
    }
    .navbar .logo span {
        font-size: 1rem;
    }

    .navbar .search .label {
        font-size: 0.6em;
    }
}
.footer {
    overflow: hidden;
    background: #1b1b1b;
    font-family: sans-serif;
    color: #cfcfcf;
    padding: 20px 0;
}

.footer .container {
    width: 92%;
    margin: 0 auto;
    overflow: hidden;
}

.footer p {
    font-size: 0.8em;
    margin-bottom: 10px;
}

.footer .generic-text {
    margin-bottom: 30px;
    border-bottom: 0.2px solid #2e2e2e;
}

.footer .stack img {
    display: inline-block;
    width: 50px;
    padding-right: 20px;
}
.order-review {
    background: white;
}
.order-review .summary-wrapper {
    padding: 20px;
    background: white;
}
.order-review .summary-wrapper h3 {
    margin-bottom: 5px;
    border-bottom: 1px solid black;
}
.order-review .order-item {
    margin-bottom: 20px;
}
.order-review p {
    text-transform: capitalize;
}
.order-review .order-address p {
    text-transform: none;
    text-transform: initial;
}
.order-review .order-address {
    margin-bottom: 50px;
}
.order-review .order-address .delivery-label {
    text-transform: uppercase;
    font-size: 0.7em;
    font-weight: bold;
}
.order-review .order-address .delivery-field {
    margin-bottom: 10px;
}
.order-review .none {
    margin-bottom: 10px;
    font-style: italic;
}
.order-review .order-total h1 {
    margin-bottom: 5px;
    border-bottom: 1px solid black;
}
.order-review .order-total p {
    font-size: 2em;
}
.place-order {
    min-height: 100vh;
    background: #e9e9e9;
    font-family: sans-serif;
}

.place-order .title h1 {
    text-align: center;
    background: black;
    color: white;
    padding: 25px;
}

.place-order .hidden {
    display: none;
}


.place-order .crust-type h2,
.place-order .crust-size h2,
.place-order .sauce h2,
.place-order .cheese h2,
.place-order .toppings h2,
.place-order .beverages h2,
.place-order .delivery-title h2,
.place-order .summary-title h2 {
    color: white;
    background: #d82a2a;
    text-align: center;
    padding: 15px 0;
}

/* errors display */
.place-order .errors {
    padding: 20px;
    background: rgb(255, 221, 221);
}
.place-order .errors h3 {
    margin-bottom: 20px;
}
.place-order .errors p {
    font-size: 0.8em;
    margin-bottom: 10px;
}


/* base options */
.place-order .base-options-list {
    list-style-type: none;
    width: 85%;
    margin: 0 auto;
    padding: 20px 0;
    display: flex;
    flex-direction: auto;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.place-order .base-option {
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 20px;
}
.place-order .base-option:last-child {
    margin-right: 0;
}

.place-order input[type="radio"] {
    display: none;
}
.place-order label {
    color: #808080;
    text-transform: capitalize;
    font-size: 1.3em;
    cursor: pointer;
    display: inline-block;
    padding: 10px 20px;
    text-align: center;
    width: 200px;
    border: 2px solid #d3d3d3;
    background: white;
    border-radius: 10px;
}
.place-order input[type="radio"]:checked+label {
    color: #d82a2a;
    border: 2px solid #d82a2a;
}
.place-order label:hover {
    border: 2px solid #db9494;
}


/* topping & beverage rulesets */
.place-order .topping-options,
.place-order .beverage-options {
    width: 99%;
    max-width: 99%;
    margin: 0 auto;
}

.place-order .topping-options-list,
.place-order .beverage-options-list {
    list-style-type: none;
    margin: 0;
    padding: 25px 0;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;
    margin: 0 auto;
    overflow: hidden;
    width: 100%;
}

.place-order .topping-option,
.place-order .beverage-option {
    width: 100%;
    overflow: hidden;
}
.place-order .topping-option h3,
.place-order .beverage-option h3 {
    text-align: center;
    text-transform: capitalize;
    padding-bottom: 10px;
}
.place-order .topping-option img {
    width: 100%;
    height: 205px;
    object-fit: fill;
    border-radius: 10px;
    overflow: hidden;
    border: 3px solid #b4898900;
    cursor: pointer;
    -webkit-filter: grayscale(0.8) brightness(0.5);
            filter: grayscale(0.8) brightness(0.5);
}
.place-order .beverage-option img {
    width: 100%;
    height: 195px;
    object-fit: fill;
    border-radius: 10px;
    overflow: hidden;
    border: 3px solid #b4898900;
    cursor: pointer;
    -webkit-filter: grayscale(0.8) brightness(0.5);
            filter: grayscale(0.8) brightness(0.5);
}
.place-order .beverage-option p {
    font-size: 0.7em;
}
.place-order .topping-option:hover img,
.place-order .beverage-option:hover img {
    -webkit-filter: grayscale(1.0) brightness(0.7);
            filter: grayscale(1.0) brightness(0.7);
}
.place-order .topping-option.selected h3,
.place-order .beverage-option.selected h3 {
    color: #d82a2a;
}
.place-order .topping-option.selected img,
.place-order .beverage-option.selected img {
    border: 3px solid #d82a2a;
    -webkit-filter: grayscale(0) brightness(1);
            filter: grayscale(0) brightness(1);
}

/* delivery info form */
.place-order .delivery-info .delivery {
    padding: 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
}
.place-order .delivery-info .delivery p {
    font-size: 0.8em;
}
.place-order .delivery-info input {
    padding: 3px;
    margin-bottom: 15px;
    width: 300px;
}
.place-order .delivery-info select {
    padding: 3px;
    margin-bottom: 15px;
    width: 200px;
}

/* order summary - confirm order */
.place-order .order-summary .summary-wrapper {
    padding: 20px;
    background: white;
}
.place-order .order-summary .summary-wrapper h3 {
    margin-bottom: 5px;
    border-bottom: 1px solid black;
}
.place-order .order-summary .order-item {
    margin-bottom: 20px;
}
.place-order .order-summary p {
    text-transform: capitalize;
}
.place-order .order-summary .order-address p {
    text-transform: none;
    text-transform: initial;
}
.place-order .order-summary .order-address {
    margin-bottom: 50px;
}
.place-order .order-summary .order-address .delivery-label {
    text-transform: uppercase;
    font-size: 0.7em;
    font-weight: bold;
}
.place-order .order-summary .order-address .delivery-field {
    margin-bottom: 10px;
}
.place-order .order-summary .none {
    margin-bottom: 10px;
    font-style: italic;
}
.place-order .order-summary .summary-wrapper .order-total h1 {
    margin-bottom: 5px;
    border-bottom: 1px solid black;
}
.place-order .order-summary .summary-wrapper .order-total p {
    font-size: 2em;
}


/* buttons */
.place-order .order-submit button,
.place-order .confirm-button button,
.place-order .back-button button {
    padding: 10px 20px;
    background: #d82a2a;
    color: white;
    font-size: 1.3em;
    cursor: pointer;
    outline: none;
    border: 1px solid #505050;
    border-radius: 10px;
}

.place-order .confirm-button {
    padding: 40px 0;
    text-align: center;
}
.place-order .back-button {
    background: white;
    width: 100%;
    margin-bottom: 40px;
}
.place-order .order-submit {
    background: white;
    width: 100%;
    padding: 50px 20px;
    text-align: right;
}


@media (max-width : 1024px) {
    .place-order .topping-options-list,
    .place-order .beverage-options-list {
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 20px;
        -webkit-column-gap: 20px;
                column-gap: 20px;
    }
}
@media (max-width : 768px) {
    .place-order .topping-option img, 
    .place-order .beverage-option img {
        height: 160px;
    }
}
@media (max-width : 600px) {
    .place-order .topping-option img, 
    .place-order .beverage-option img {
        height: 160px;
    }
    .place-order .topping-options-list,
    .place-order .beverage-options-list {
        grid-template-columns: repeat(3, 1fr);
    }
    .place-order .order-submit {
        text-align: center;
    }
}

@media (max-width : 550px) {
    .place-order .base-options-list {
        flex-direction: column;
        align-items: center;
    }
    .place-order .base-option {
        margin-right: 0;
        margin-bottom: 20px;
    }
    .place-order .topping-options,
    .place-order .beverage-options {
        width: 85%;
        max-width: 85%;
    }
    .place-order .topping-options-list,
    .place-order .beverage-options-list {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 20px;
        -webkit-column-gap: 20px;
                column-gap: 20px;
    }
    .place-order .topping-option img, 
    .place-order .beverage-option img {
        height: 155px;
    }
}
@media (max-width : 500px) {
    .place-order .topping-option img, 
    .place-order .beverage-option img {
        height: 105px;
    }
    .place-order .delivery-info .delivery {
        padding: 20px 0;
    }
}
.view-orders {
    overflow: hidden;
    width: 100%;
    min-height: 100vh;
    background: white;
    font-family: sans-serif;
}

.view-orders .order-content {
    margin-bottom: 30px;
}

.view-orders .your-orders h1 {
    background: black;
    color: white;
    text-align: center;
    padding: 25px;
}
.view-orders .your-orders h1 span {
    display: inline-block;
    font-style: italic;
    font-weight: normal;
    font-size: 0.8em;
}

.view-orders h2 {
    padding: 15px;
    color: white;
    background: #d82a2a;
}
.view-orders h2 span {
    display: inline-block;
    font-style: italic;
    font-weight: normal;
    font-size: 0.6em;
}
.view-orders h3 {
    border-bottom: 1px solid black;
}

.view-orders .order-review {
    padding: 20px;
}

.view-orders .cancel-order {
    text-align: right;
    padding: 20px;
}
.view-orders .cancel-order-btn {
    padding: 10px 20px;
    background: #d82a2a;
    color: white;
    font-size: 1.3em;
    cursor: pointer;
    outline: none;
    border: 1px solid #505050;
    border-radius: 10px;
    text-align: right;
}
.home {
    background-color: white;
    font-family: sans-serif;
    height: 100vh;
    min-height: 100vh;
    z-index: 1;
}

.home .order-pizza a {
    display: block;
    padding: 50px 25px;
    background-image: url("/images/order/pizza-bg.jpg");
    background-repeat: no-repeat;
    background-position: center top;
    height: 50vh;
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    position: relative;
}

.home .order-pizza h1 {
    letter-spacing: 3px;
    background: #d82a2a;
    position: absolute;
    top: 4.5em;
    transform: skewY(-7deg);
    padding: 10px 5px;
    padding-right: 5vw;
}
.home .order-pizza h3 {
    background: #49a167;
    position: absolute;
    bottom: 25px;
    padding: 15px 10px;
    border-radius: 3px;
}

.home .lookup-order {
    padding: 20px 0;
    text-align: center;
    background: #d82a2a;
}
.home .lookup-order h2 {
    color: white;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
}
.home .lookup-order .nav-search {
    margin: 0 auto;
}
.home .lookup-order .nav-search .search-box input[type="text"] {
    padding: 12px;
    font-size: 1.3em;
}

.home .lookup-order .nav-search .search-btn button {
    padding: 12px;
    font-size: 1.3em;
}

.home .filler {
    z-index: -2;
    background: linear-gradient(
        rgba(0, 0, 0, 0.612), 
        rgba(0, 0, 0, 0.905)
    ), 
    url("/images/pizzashop.jpg");
    background-position: center top;
    background-size: 100% 40%;
    height: 100%;
}
.home .filler p {
    z-index: 0;
    font-size: 30px;
    letter-spacing: 3px;
    color: white;
    font-family: 'Shadows Into Light', cursive;
    position: relative;
    top: 8vh;
    left: 1em;
    padding: 0 50px 0 20px;
    background: black;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transform: skewY(-6deg);
}



@media (max-width : 550px) {
    .home .order-pizza a {
        height: 80vh;
    }
    .home .lookup-order .nav-search .search-box input[type="text"] {
        padding: 8px;
        font-size: 1em;
    }
    
    .home .lookup-order .nav-search .search-btn button {
        padding: 8px;
        font-size: 1em;
    }
    .home .filler p {
        display: none;
    }
    
}
.not-found {
    background: white;
    font-family: sans-serif;
    min-height: 100vh;
    padding: 20px;
}

.not-found h1 {
    padding-top: 10vh;
    padding-bottom: 25px;
}
/* Global Rulesets, Resets */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*:before,
*:after {
    box-sizing: border-box;
}

body {
    background: linear-gradient(
        rgba(0, 0, 0, 0.612), 
        rgba(0, 0, 0, 0.905)
      ), 
      url("/images/site-bg.jpg") no-repeat center top;
    background-color: black;
    background-attachment: fixed;
}

html {
    height: 100%;
}
.app {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.app-container {
    width: 60%;
    margin: 0 auto;
    overflow: hidden;
    box-shadow: 1px 1px 45px black, -1px -1px 45px black;
}

@media (max-width : 1600px) {
    body {
        background: none;
        background-color: white;
    }
    .app-container {
        width: 100%;
        box-shadow: none;
    }
}
